.header__link {
    font-family: 'Inter';
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
    color: white;
    text-decoration: none;
    transition: .3s linear;
}

.header__link:hover {
    opacity: 0.6;
}

@media(max-width: 350px) {
    .header__link {
        font-size: 14px;
        line-height: 17px;
    }
}