.header__button {
    width: 35px;
    height: 100%;
    border: none;
    background-image: url(../../../images/menuButton.svg);
    background-size: cover;
    display: none;
}

@media(max-width: 500px){
    .header__button{
        height: 24px;
        width: 23px;
        display: block;
    }
}