.header__menu{
    flex-direction: row;
    align-items: center;
}

@media(max-width: 500px){
    .header__menu{
        flex-direction: column;
        align-items: center;
        order: -1;
        border-bottom: 1px solid rgba(84, 84, 84, .7);
        padding-bottom: 40px;
        margin-bottom: 27px;
        position: absolute;
        transform: translateY(-100%);
    }
}